@font-face {
  font-family: "din";
  src: url(/portfolio/static/media/DINCondensed-Bold.4675a4b4.woff) format("woff")
}

@font-face {
  font-family: "dinAlt";
  src: url(/portfolio/static/media/DINAlternate-Bold.e0d33840.woff) format("woff")
}

.white {
  color: white
}

.black {
  color: black
}

* {
  box-sizing: border-box;
}

.fill {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: hotpink;
}

.main {
  position: relative;
  width: 200px;
  height: 50px;
  background: #272727;
  cursor: pointer;
  border-radius: 5px;
  border: 2px solid white;
  overflow: hidden;
}

.trails-main {
  overflow: hidden;
}

.trails-text {
  /* position: relative; */
  /* width: 100%; */
  height: 80px;
  /* font-size: 5em; */
  /* font-weight: 800; */
  will-change: transform, opacity;
  overflow: hidden;
}

.trails-text > div {
  overflow: hidden;
}

